<template>
  <div style="padding-bottom:20px;background:#f1eff2;">
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
      <a @click="()=>{
          $router.go(-1)
        }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>日志管理</span>
    </mu-appbar>
    <div class="contanerMain">
      <!-- <select name="" id=""></select> -->
      <div>
        <div v-for="(item,index) in DataList1" :key="index">
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">操作时间</span>
            <span>{{item.gmtCreate}}</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">操作类型</span>
            <span>{{item.type}}</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">IP地址</span>
            <span>{{item.ip}}</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">操作用户</span>
            <span style="color:#ccc">暂无。。。</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">操作状态</span>
            <span>{{item.status}}</span>
          </div>
          <div class="eventDetailsDataList1">
            <span style="color:#7a7a7a">操作描述</span>
            <textarea name id="textarea" disabled cols="30" rows="10" :value="item.content"></textarea>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  name: "maintenanceChild3",
  data() {
    return {
      panel: "",
      DataList: [],
      id: "",
      DataList1: [],
      loading: false,
      token:""
    };
  },
  methods: {
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    fullscreen() {
      this.loading = this.$loading();
    },
    async initData(e) {
      this.fullscreen();
      var url = "/job/sys-log";
      var pageSize = 100000000;
      var pageNum = 1;
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageSize,
          pageNum
        },
        this.token
      );
      console.log(res);
      if (res.data.message === "success") {
        this.loading.close();
      } else {
        this.loading.close();
      }
      var arr = res.data.data.rows;
      for (let index = 0; index < arr.length; index++) {
        const element = arr[index].id;
        if (element === e) {
          this.DataList1.push(arr[index]);
        } else {
        }
      }
    }
  },
  mounted() {
    var id = this.$route.query.id;
    this.token=sessionStorage.getItem("token");
    this.initData(id);
  }
};
</script>
<style scoped>
.contanerMain {
  margin: 0 5px;
  background: white;
  padding: 10px 20px;
  border-radius: 10px;

  margin-top: 10px;
  /* margin-bottom: 50px; */
}
.eventDetailsDataList {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 40px;
  align-items: center;
  border-bottom: 1px solid rgb(238, 236, 236);
}
.header {
  padding-left: 10px;
}
.headerContent {
  margin-top: 10px;
}
#textarea {
  width: 100%;
  height: 100px;
  outline: none;
  border-radius: 10px;
  border: #ccc 1px solid;
  padding: 10px;
  background: rgb(246, 249, 252);
}
.eventDetailsDataList1 {
  margin-top: 10px;
}
</style>



